const store_key = 'todolist-vue'
export default{
    fetch () {
        return JSON.parse(window.localStorage.getItem(store_key) || '[]')
    },
    save (lists) {
        window.localStorage.setItem(store_key, JSON.stringify(lists))
    }
}
